._page_tabwrap {
    padding: 20px 0px;
}

._page_tabwrap>ul.nav {
    position: relative;
}

._page_tabwrap>ul.nav:after {
    content: " ";
    height: 2px;
    width: 90%;
    position: absolute;
    left: 51%;
    top: 30%;
    z-index: 0;
    background: #bdbdbd;
    transform: translate(-50%, -50%);
}

._page_tabwrap .nav .nav-item {
    position: relative;
    z-index: 1;
}

._page_tabwrap ul>li>a h6 span {
    color: #fff;
    background: #6C7481;
    border-radius: 50%;
    width: 31px !important;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid white;
    outline: 1px solid #6C7481;
}

._page_tabwrap ul>li>a.active h6 span {
    color: #fff;
    background: #4e73df;
    border-radius: 50%;
    width: 31px !important;
    height: 32px;
    display: flex;
    align-items: center;
