/*upload css start here*/
.Neon {
    font-family: sans-serif;
    font-size: 14px;
    color: #494949;
    position: relative;

}

.Neon {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.customNeon .Neon-input-dragDrop {
    height: 300px;
}

.Neon-input-dragDrop {
    box-shadow: 0 10px 60px rgb(218 229 255);
    border: 2px solid #56b5eb;
    border-radius: 24px;
    display: block;
    /* width: 316px; */
    width: 100%;
    height: 320px;
    /* padding: 35px 25px; */
    color: #8d9499;
    color: #97A1A8;
    background: #FFFFFF;
    border: 1px dashed #DBDBE4;
    border-radius: 20px;
    text-align: center;
    -webkit-transition: box-shadow 0.3s, border-color 0.3s;
    -moz-transition: box-shadow 0.3s, border-color 0.3s;
    transition: box-shadow 0.3s, border-color 0.3s;
    margin-right: auto !important;
    margin-left: auto !important;
    display: grid;
    align-content: center;
    box-shadow: 0 10px 60px rgb(218, 229, 255);
    border: 2px solid #3aaae54d;
    border-radius: 24px;
    padding: 10px 31px;

}

.Neon-input-icon img {
    width: 70px;
}

.Neon-input-inner {
    border: 2px dashed #3aaae5;
    border-radius: 15px;
    padding: 30px;
}

.Neon-input-inner p {
    font-family: 'Plus Jakarta Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 29px;
    text-align: center;
    max-width: 330px;
    color: #4D4D4D;
    text-align: center;
    margin: 0 auto;
    height: 57px;
    overflow: hidden;
}

.Neon-input-inner p a {
    color: #138BFB;
}

.Neon-input-inner p a:hover {
    text-decoration: none;
}

.Neon-input-dragDrop .Neon-input-icon {
    font-size: 48px;
    /* margin-top: 20px; */
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.Neon-input-text h3 {
    margin: 0;
    font-size: 18px;
}

.Neon-input-text span {
    font-size: 12px;
}

.Neon-input-choose-btn.blue {
    color: #008BFF;
    border: 1px solid #008BFF;
}

.Neon-input-choose-btn {
    display: inline-block;
    padding: 8px 14px;
    outline: none;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
    white-space: nowrap;
    font-size: 12px;
    font-weight: bold;
    color: #8d9496;
    border-radius: 3px;
    border: 1px solid #c6c6c6;
    vertical-align: middle;
    background-color: #fff;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.05);
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    transition: all 0.2s;
}

