    }

    ._active_courseswrap {

        display: flex;
        flex-direction: column;
    }

    ._active_courseswrap select {
        margin-bottom: 15px;
    }

    ._notification_wrap {
        margin-bottom: 30px;
    }

    ._headeing_wrap {
        flex-direction: column;
    }

    ._edit_texttl p {
        width: 60%;
    }

    .evolving_wrap {
        flex-direction: column;
    }

    .evolving_wrap img {
        height: 150px;
        margin-top: 20px;
    }

    ._add_sectionwrp {
        height: 370px;
    }

    ._Add_ContentBox {
        margin: 10px;
    }

    ._add_sectionwrp .nav-pills .nav-link.active:before {
        display: none;
    }

    ._add_sectionwrp .nav-pills .nav-link.active {
        border-radius: 14px 14px 14px 14px;
    }

    ._Add_Content {
        height: 200px;
    }

    ._btn_video {
        margin: 10px 0;
    }

    .__engage_img {
        position: relative;
        height: 116px;
    }

    .__engage_info h4 {
        font-size: 20px;
        line-height: 25px;
        margin-bottom: 0;
    }

    .__engage_info {
        padding: 15px 15px;
    }

    .btn_started_own {
        margin-top: 15px;
    }

    ._engage_dspl_own {
        display: flex;
        justify-content: center;
        margin: 0 auto;
    }
}

@media screen and (max-width: 460px) {
    ._page_tabwrap>ul.nav:after {
        width: 65%;
        content: " ";
    }
}

@media only screen and (max-width:383px) {
